import './TodoInfo.scss';

export const TodoInfo = ({ todo }) => (
  <article className={
    todo.completed ? 'TodoInfo TodoInfo--completed' : 'TodoInfo'
  }
  >
    <h2 className="TodoInfo__title">{todo.title}</h2>

    <a className="UserInfo" href={`mailto:${todo.user.email}`}>
      {todo.user.name}
    </a>
  </article>
);
